.friend_list {
    margin: 0 auto 50px;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .item {
    display: flex;
    align-items: center;
    width: 280px;
    height: 100px;
    padding: 10px 20px;
  
    background-color: #ffffff;
    border-radius: 5px;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.55);
  }
  
  .item:not(:last-child) {
    margin-bottom: 10px;
  }
  
  .status {
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin-right: 20px;
  }
  
  .avatar {
    margin-right: 20px;
  }
  
  .online {
    background-color: green;
  }
  
  .offline {
    background-color: red;
  }
  
  .name {
    font-size: 26px;
    font-weight: 500;
  }